/* navbar */
.navbar{
  color: var(--secColor);
}
.hero-btn{
  background-color: var(--terColor);
}
.hero-btn:hover{
  color:var(--terColor);
  border: 1px solid var(--secColor);
}
/* hero */
  .hero{
    margin-top: 150px;
    margin-bottom: 120px;
   
  }
  .hello{
    font-size: 1rem;
  }
  .intro{
    font-size: 1.4rem;
  }
/* work  */
.work{

}
.project{
  width: 100%;
  margin-bottom: 3rem;
}
.project-desc{
  padding: 20px;
  opacity: 0;
  width: 100%;
  height: 100%;
  border-radius: 1rem;
  background-color:rgba(0, 0, 0, 0.65);
  
}
.project-desc:hover{
  
  opacity: 1;
  visibility: visible;
}
.project-title>h1{
  font-size: 1.2rem;
}
.links{
  margin-bottom: 1rem;
}
.circle{
  margin-right: -10px;
  height: 64px;
  width: 64px;
  border-radius: 50%;
  background-color: #000000;
  z-index: 1;
}
.github{
  width: 36px;
  height: 36px;
}
.circle1{
  height: 64px;
  width: 64px;
  border-radius: 50%;
  z-index: 0;
  background-color: var(--terColor);
}

.link{
  width: 36px;
}
.project-title>p{
  font-size: 1rem;
}

.more{
  margin-top: -3.8rem;
}
.image-size{
  width: 97%;
  height: 467px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  border-radius: 1rem;
  margin-bottom:3.813rem;
}
.project1{
  background-image: url("/public/1.png");
 
}
.project2{
  background-image: url("/public/2.png");
}
