/* services */
.services{
  margin-top: 3.813rem;
}
.colorText{
  color: var(--terColor);
}
.service-heading{
  margin-bottom: 71px;
}
.service-border{
  border-left: 2px solid var(--terColor); 
}
.service1,.service2,.service3{
  width: 324px;
  height: 450px;
  margin-bottom: 5rem;
}
.service1-logo{
    width:65px;
    height: 52px;
}
.service-name{
  margin-left: 22px;
  margin-bottom: 30px;
}
.service-number{
  margin-top: 50px;
  margin-left: 45px;
  font-size: 1.2rem;
  margin-bottom: 112px;
  color:var(--terColor)
}
.service-text{
  margin-left: 55px;
}
.service-name-heading{
  margin-left: 22px;
  font-size: 1.7rem;
}
/* contact */
  .contact{
    margin-top: 120px;
  }
  .social>div{
    margin: 1rem;
  }
  .social-logo{
    width: 50px;
    height: 50px;
    background-repeat: no-repeat;
    background-size: contain;
  }
  .call-logo{
    background-image: url('/public//call.png');
  }
  .call-logo:hover{
   background-image:url('/public/call1.png');
  }
  .instagram-logo{
    background-image: url('/public/github-box.png');
  }
  .instagram-logo:hover{
    background-image:url('/public/github-box1.png');
